.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
    }

    &__link {
      position: relative;
      color:inherit;
      text-decoration: none;
      font-size: 22px;
      letter-spacing: 2px;
      line-height: 22px;

    &::after {
      content: '';
      bottom: -4px;
      position: absolute;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $main-text-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}

